function loadSettings() {
    const apiKey = localStorage.getItem('apiKey') || '';
    const formattingModel = localStorage.getItem('formattingModel') || 'openai/gpt-4-mini';
    const applyFormatting = localStorage.getItem('applyFormatting') === 'true';

    document.getElementById('api-key').value = apiKey;
    $('#formatting-model').val(formattingModel).trigger('change');
    document.getElementById('apply-formatting').checked = applyFormatting;
}



 function populateModelSelectt(models) {
    const promptModelSelect = $('#prompt-model');
    const formattingModelSelect = $('#formatting-model');

    [promptModelSelect, formattingModelSelect].forEach(select => {
        select.empty();
        models.forEach(model => {
            const option = new Option(model.name, model.id, false, false);
            select.append(option);
        });
        select.trigger('change');
    });

    //      formatting-model
    const defaultModel = models.find(model => model.id === 'openai/gpt-4-mini') || models[0];
    if (defaultModel) {
        formattingModelSelect.val(defaultModel.id).trigger('change');
    }

    //   ,   
    const savedModel = localStorage.getItem('formattingModel');
    if (savedModel && models.some(model => model.id === savedModel)) {
        formattingModelSelect.val(savedModel).trigger('change');
    }
}



function saveApiKey() {
    const apiKey = document.getElementById('api-key').value;
    localStorage.setItem('apiKey', apiKey);
}




const OPENROUTER_BASE_URL = "https://openrouter.ai/api/v1";

 async function loadModels() {
    try {
        const response = await axios.get(`${OPENROUTER_BASE_URL}/models`);
        const models = response.data.data;
        localStorage.setItem('models', JSON.stringify(models));
        populateModelSelectt(models);
    } catch (error) {
        console.error('Error loading models:', error);
    }
}

 async function generateTextWithAI(systemPrompt, userPrompt, model, signal) {
    const apiKey = localStorage.getItem('apiKey');

    try {
        const response = await fetch(`${OPENROUTER_BASE_URL}/chat/completions`, {
            method: 'POST',
            signal,
            headers: {
                'Authorization': `Bearer ${apiKey}`,
                'Content-Type': 'application/json',
                'HTTP-Referer': 'https://www.office.com',
                'X-Title': 'Word AI Assistant',
            },
            body: JSON.stringify({
                model: model,
                messages: [
                    { role: "system", content: systemPrompt },
                    { role: "user", content: userPrompt }
                ],
            }),
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        return data.choices[0].message.content.trim();
    } catch (error) {
        console.error('Error in generateTextWithAI:', error);
        throw error;
    }
}

 async function generateTextWithAIFormat(systemPrompt, userPrompt, model, signal) {
    const apiKey = localStorage.getItem('apiKey');

    try {
        const response = await fetch(`${OPENROUTER_BASE_URL}/chat/completions`, {
            method: 'POST',
            signal,
            headers: {
                'Authorization': `Bearer ${apiKey}`,
                'Content-Type': 'application/json',
                'HTTP-Referer': 'https://www.office.com',
                'X-Title': 'Word AI Assistant',
            },
            body: JSON.stringify({
                model: model,
                messages: [
                    { role: "system", content: systemPrompt },
                    { role: "user", content: userPrompt }
                ],
            }),
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        return data.choices[0].message.content.trim();
    } catch (error) {
        console.error('Error in generateTextWithAI:', error);
        throw error;
    }
}



let originalText = "";
let currentController = null;
let lastSelectedPrompt = null;

