  const homeTab = document.getElementById('home');
  homeTab.innerHTML = `
      <select id="prompt-select" class="form-select mt-3"></select>
      <div id="selected-model" class="mt-2"></div>
      <button id="replace-text" class="btn btn-primary mt-3" disabled>  </button>
      <button id="cancel-request" class="btn btn-secondary mt-3" style="display: none;"> </button>
      <button id="show-selected-text" class="btn btn-info mt-3">    </button>
      <!-- <button id="apply-tricks" class="btn btn-warning mt-3"> </button>
      <button id="repeat-styles" class="btn btn-primary mt-3"> </button> -->
      <div id="selected-text-info" class="mt-3"></div>
  `;

  $('#prompt-select').select2({
      width: '100%',
      placeholder: ' ',
      templateResult: formatPrompt,
      templateSelection: formatPromptSelection,
  });

  document.getElementById("replace-text").onclick = replaceSelectedText;
  document.getElementById("cancel-request").onclick = cancelRequest;
  document.getElementById("show-selected-text").onclick = showSelectedText;
