function populateGrammarModelSelect() {
    const select = $('#grammar-model');
    const models = JSON.parse(localStorage.getItem('models') || '[]');
    console.log('Loaded models:', models); //   
    select.empty();
    let defaultModelId = null;
    models.forEach((model, index) => {
        const option = new Option(model.name, model.id, false, false);
        select.append(option);
        //  defaultModelId   ,     
        if (!defaultModelId) {
            defaultModelId = model.id;
        }
        //        ,     
        if (model.id === 'anthropic/claude-3.5-sonnet:beta') {
            defaultModelId = model.id;
        } 
        // 
    });
    console.log('Default model ID:', defaultModelId); //  ID   

    const savedModel = localStorage.getItem('grammarModel');
    console.log('Saved model:', savedModel); //   

    if (savedModel && models.some(model => model.id === savedModel)) {
        select.val(savedModel).trigger('change');
        console.log(`Selected saved model: ${savedModel}`);
    } else if (defaultModelId) {
        select.val(defaultModelId).trigger('change');
        console.log(`Selected default model: ${defaultModelId}`);
    } else {
        console.log('No models available to select');
    }
}



