function populatePresetSelectGrammar() {
    const select = $('#preset-select');
    select.empty();

    //   " "
    const currentOption = new Option(' ', 'current', false, false);
    select.append(currentOption);

    let presets = JSON.parse(localStorage.getItem('grammarPresets') || '[]');
    let lastSelectedPreset = localStorage.getItem('lastSelectedPreset') || null;

    presets.forEach(preset => {
        const option = new Option(preset.name, preset.name, false, false);
        select.append(option);
    });

    if (lastSelectedPreset && lastSelectedPreset !== 'current') {
        select.val(lastSelectedPreset).trigger('change');
    } else {
        //  " ",     
        select.val('current').trigger('change');
    }
}



function getBooleanSettingGrammar(key, defaultValue) {
    const value = localStorage.getItem(key);
    if (value === null || value === undefined) {
        return defaultValue;
    }
    return (value === 'true');
}

function getNumericSettingGrammar(key, defaultValue) {
    const value = localStorage.getItem(key);
    if (value === null || value === undefined) {
        return defaultValue;
    }
    const num = parseFloat(value);
    return isNaN(num) ? defaultValue : num;
}

function loadGrammarSettings() {
    const lastSelectedPreset = localStorage.getItem('lastSelectedPreset');
    if (lastSelectedPreset && lastSelectedPreset !== 'current') {
        let presets = JSON.parse(localStorage.getItem('grammarPresets') || '[]');
        const preset = presets.find(p => p.name === lastSelectedPreset);
        if (preset) {
            $('#preset-select').val(preset.name).trigger('change');
            loadSelectedPresetGrammar();
            return;
        } else {
            localStorage.removeItem('lastSelectedPreset');
            //  " ",    
            $('#preset-select').val('current').trigger('change');
        }
    } else {
        //  " ",     
        $('#preset-select').val('current').trigger('change');
    }

    //     localStorage
    const highlight = getBooleanSettingGrammar('highlightErrors', true);
    document.getElementById('highlight-checkbox').checked = highlight;

    const addComments = getBooleanSettingGrammar('addComments', false);
    document.getElementById('comment-checkbox').checked = addComments;

    const highlightCorrected = getBooleanSettingGrammar('highlightCorrected', true);
    document.getElementById('highlight-corrected-checkbox').checked = highlightCorrected;

    const checkGrammatical = getBooleanSettingGrammar('checkGrammatical', false);
    const checkSyntactic = getBooleanSettingGrammar('checkSyntactic', false);
    const checkStylistic = getBooleanSettingGrammar('checkStylistic', true);
    const checkLogical = getBooleanSettingGrammar('checkLogical', true);

    document.getElementById('check-grammatical').checked = checkGrammatical;
    document.getElementById('check-syntactic').checked = checkSyntactic;
    document.getElementById('check-stylistic').checked = checkStylistic;
    document.getElementById('check-logical').checked = checkLogical;

    const splitIntoChunks = getBooleanSettingGrammar('splitIntoChunks', false);
    document.getElementById('split-into-chunks-checkbox').checked = splitIntoChunks;

    const chunkSize = getNumericSettingGrammar('chunkSize', 8);
    document.getElementById('chunk-size').value = chunkSize;

    const maxChars = getNumericSettingGrammar('maxChars', 2000);
    document.getElementById('max-chars').value = maxChars;

    const temperature = getNumericSettingGrammar('temperature', 0.1);
    document.getElementById('temperature').value = temperature;

    const maxTokens = getNumericSettingGrammar('maxTokens', 4000);
    document.getElementById('max-tokens').value = maxTokens;

    const responseFormat = getBooleanSettingGrammar('responseFormat', false);
    document.getElementById('response-format-checkbox').checked = responseFormat;

    const retryOnError = getBooleanSettingGrammar('retryOnError', true);
    document.getElementById('retry-on-error-checkbox').checked = retryOnError;

    const retryCount = getNumericSettingGrammar('retryCount', 3);
    document.getElementById('retry-count').value = retryCount;

    const editPrompts = getBooleanSettingGrammar('editPrompts', true);
    document.getElementById('edit-prompts-checkbox').checked = editPrompts;
    togglePromptsEditorGrammar();
}


