function saveCurrentSettingsAsPresetGrammar() {
    const presetNameInput = document.getElementById('preset-name');
    const presetName = presetNameInput.value.trim();
    if (!presetName) {
        displayPresetMessageGrammar('     .', 'danger');
        return;
    }

    if (presetName.toLowerCase() === 'current' || presetName === ' ') {
        displayPresetMessageGrammar('     " ".', 'danger');
        return;
    }

    const preset = {
        name: presetName,
        settings: getCurrentSettingsGrammar(),
        prompts: getCurrentPromptsGrammar(),
    };

    let presets = JSON.parse(localStorage.getItem('grammarPresets') || '[]');

    // ,      
    const existingIndex = presets.findIndex(p => p.name === presetName);
    if (existingIndex !== -1) {
        //    
        showPresetConfirmationGrammar(`   "${presetName}"  .  ?`, () => {
            presets[existingIndex] = preset;
            localStorage.setItem('grammarPresets', JSON.stringify(presets));
            populatePresetSelectGrammar();

            //     
            $('#preset-select').val(preset.name).trigger('change');
            localStorage.setItem('lastSelectedPreset', preset.name);

            displayPresetMessageGrammar(' .', 'success');

            //     
            presetNameInput.value = '';

            //  JSON 
            updatePresetJSONGrammar();
        });
    } else {
        presets.push(preset);
        localStorage.setItem('grammarPresets', JSON.stringify(presets));
        populatePresetSelectGrammar();

        //     
        $('#preset-select').val(preset.name).trigger('change');
        localStorage.setItem('lastSelectedPreset', preset.name);

        displayPresetMessageGrammar(' .', 'success');

        //     
        presetNameInput.value = '';

        //  JSON 
        updatePresetJSONGrammar();
    }
}




function showPresetConfirmationGrammar(message, onConfirm) {
    const confirmationDiv = document.getElementById('preset-confirmation');
    const confirmationMessage = document.getElementById('preset-confirmation-message');
    const confirmYesButton = document.getElementById('preset-confirm-yes');
    const confirmNoButton = document.getElementById('preset-confirm-no');

    confirmationMessage.textContent = message;
    confirmationDiv.style.display = 'block';

    //    
    confirmYesButton.onclick = null;
    confirmNoButton.onclick = null;

    confirmYesButton.onclick = () => {
        confirmationDiv.style.display = 'none';
        onConfirm();
    };

    confirmNoButton.onclick = () => {
        confirmationDiv.style.display = 'none';
    };
}

//  :         

function setupSettingsChangeHandlersGrammar() {
    //     
    function onSettingsChange() {
        if (window.isLoadingPreset) {
            return;
        }
    
        saveGrammarSettings();
        saveGrammarPrompts();
    
        const currentSettings = getCurrentSettingsGrammar();
        const currentPrompts = getCurrentPromptsGrammar();
        let matchedPresetName = null;
    
        let presets = JSON.parse(localStorage.getItem('grammarPresets') || '[]');
        for (let preset of presets) {
            if (
                areSettingsEqual(preset.settings, currentSettings) &&
                areSettingsEqual(preset.prompts, currentPrompts)
            ) {
                matchedPresetName = preset.name;
                break;
            }
        }
    
        if (matchedPresetName) {
            //       'change'
            $('#preset-select').val(matchedPresetName).trigger('change');
            localStorage.setItem('lastSelectedPreset', matchedPresetName);
        } else {
            //  " ",    
            $('#preset-select').val('current').trigger('change'); //  .trigger('change')
            localStorage.removeItem('lastSelectedPreset');
        }
    
        //  JSON 
        updatePresetJSONGrammar();
    }

    const settingsElements = [
        'highlight-checkbox',
        'comment-checkbox',
        'highlight-corrected-checkbox',
        'check-grammatical',
        'check-syntactic',
        'check-stylistic',
        'check-logical',
        'split-into-chunks-checkbox',
        'chunk-size',
        'max-chars',
        'temperature',
        'max-tokens',
        'response-format-checkbox',
        'retry-on-error-checkbox',
        'retry-count',
        'edit-prompts-checkbox',
    ];

    settingsElements.forEach(id => {
        const element = document.getElementById(id);
        if (element) {
            element.addEventListener('change', onSettingsChange);
        }
    });

    //    
    const promptElements = [
        'prompt-role',
        'prompt-grammatical',
        'prompt-syntactic',
        'prompt-stylistic',
        'prompt-logical',
        'prompt-text-note',
    ];

    promptElements.forEach(id => {
        const element = document.getElementById(id);
        if (element) {
            element.addEventListener('input', onSettingsChange);
        }
    });

    //   
    $('#grammar-model').on('change', onSettingsChange);

}


function areSettingsEqual(obj1, obj2) {
    const obj1Keys = Object.keys(obj1).sort();
    const obj2Keys = Object.keys(obj2).sort();

    if (obj1Keys.length !== obj2Keys.length) {
        return false;
    }

    for (let i = 0; i < obj1Keys.length; i++) {
        const key = obj1Keys[i];
        if (key !== obj2Keys[i]) {
            return false;
        }
        const val1 = obj1[key];
        const val2 = obj2[key];

        if (typeof val1 === 'object' && typeof val2 === 'object') {
            if (!areSettingsEqual(val1, val2)) {
                return false;
            }
        } else if (val1 !== val2) {
            return false;
        }
    }
    return true;
}





function loadSelectedPresetGrammar() {
    window.isLoadingPreset = true;

    const presetName = $('#preset-select').val();
    if (!presetName || presetName === 'current') {
        //   " ",   
        window.isLoadingPreset = false;
        return;
    }

    let presets = JSON.parse(localStorage.getItem('grammarPresets') || '[]');
    const preset = presets.find(p => p.name === presetName);
    if (!preset) {
        window.isLoadingPreset = false;
        return;
    }

    //    
    const settings = preset.settings;
    document.getElementById('highlight-checkbox').checked = settings.highlightErrors;
    document.getElementById('comment-checkbox').checked = settings.addComments;
    document.getElementById('highlight-corrected-checkbox').checked = settings.highlightCorrected;
    document.getElementById('check-grammatical').checked = settings.checkGrammatical;
    document.getElementById('check-syntactic').checked = settings.checkSyntactic;
    document.getElementById('check-stylistic').checked = settings.checkStylistic;
    document.getElementById('check-logical').checked = settings.checkLogical;
    document.getElementById('split-into-chunks-checkbox').checked = settings.splitIntoChunks;
    document.getElementById('chunk-size').value = settings.chunkSize;
    document.getElementById('max-chars').value = settings.maxChars;
    document.getElementById('temperature').value = settings.temperature;
    document.getElementById('max-tokens').value = settings.maxTokens;
    document.getElementById('response-format-checkbox').checked = settings.responseFormat;
    document.getElementById('retry-on-error-checkbox').checked = settings.retryOnError;
    document.getElementById('retry-count').value = settings.retryCount;
    document.getElementById('edit-prompts-checkbox').checked = settings.editPrompts;
    togglePromptsEditorGrammar();

    //  
    const prompts = preset.prompts;
    document.getElementById('prompt-role').value = prompts.promptRole;
    document.getElementById('prompt-grammatical').value = prompts.promptGrammatical;
    document.getElementById('prompt-syntactic').value = prompts.promptSyntactic;
    document.getElementById('prompt-stylistic').value = prompts.promptStylistic;
    document.getElementById('prompt-logical').value = prompts.promptLogical;
    document.getElementById('prompt-text-note').value = prompts.promptTextNote;

    //   
    if (settings.grammarModel) {
        $('#grammar-model').val(settings.grammarModel).trigger('change');
        //    populateGrammarModelSelect(),  
        populateGrammarModelSelect();
    }

    //    
    localStorage.setItem('lastSelectedPreset', presetName);

    //      localStorage
    saveGrammarSettings();
    saveGrammarPrompts();

    displayPresetMessageGrammar(' .', 'success');

    //  JSON
    updatePresetJSONGrammar();

    window.isLoadingPreset = false;
}





function deleteSelectedPresetGrammar() {
    const presetName = $('#preset-select').val();
    if (!presetName || presetName === 'current') {
        displayPresetMessageGrammar('    .', 'danger');
        return;
    }

    showPresetConfirmationGrammar(` ,     "${presetName}"?`, () => {
        let presets = JSON.parse(localStorage.getItem('grammarPresets') || '[]');
        presets = presets.filter(p => p.name !== presetName);
        localStorage.setItem('grammarPresets', JSON.stringify(presets));

        //   
        $('#preset-select').val('current').trigger('change');
        localStorage.removeItem('lastSelectedPreset');

        populatePresetSelectGrammar();

        displayPresetMessageGrammar(' .', 'success');

        //   JSON
        document.getElementById('preset-json').value = '';
    });
}

function updatePresetJSONGrammar() {
    const presetName = $('#preset-select').val();
    if (!presetName || presetName === 'current') {
        document.getElementById('preset-json').value = '';
        return;
    }

    let presets = JSON.parse(localStorage.getItem('grammarPresets') || '[]');
    const preset = presets.find(p => p.name === presetName);
    if (!preset) {
        document.getElementById('preset-json').value = '';
        return;
    }

    const presetJSON = JSON.stringify(preset, null, 2);
    document.getElementById('preset-json').value = presetJSON;
}




function copyPresetJSONGrammar() {
    const presetJSONTextarea = document.getElementById('preset-json');
    presetJSONTextarea.select();
    document.execCommand('copy');
    displayPresetMessageGrammar('JSON     .', 'success');
}

function importPresetFromJSONGrammar() {
    const presetJSON = document.getElementById('preset-json-import').value;
    if (!presetJSON) {
        displayPresetMessageGrammar(' JSON .', 'danger');
        return;
    }

    try {
        const preset = JSON.parse(presetJSON);
        if (!preset.name || !preset.settings || !preset.prompts) {
            displayPresetMessageGrammar('  .', 'danger');
            return;
        }

        if (preset.name.toLowerCase() === 'current' || preset.name === ' ') {
            displayPresetMessageGrammar('     " ".', 'danger');
            return;
        }

        preset.settings = { ...getDefaultSettingsGrammar(), ...preset.settings };
        preset.prompts = { ...getDefaultPromptsGrammar(), ...preset.prompts };

        let presets = JSON.parse(localStorage.getItem('grammarPresets') || '[]');

        // ,      
        const existingIndex = presets.findIndex(p => p.name === preset.name);
        if (existingIndex !== -1) {
            showPresetConfirmationGrammar(`   "${preset.name}"  .  ?`, () => {
                presets[existingIndex] = preset;
                localStorage.setItem('grammarPresets', JSON.stringify(presets));
                populatePresetSelectGrammar();
                $('#preset-select').val(preset.name).trigger('change');
                localStorage.setItem('lastSelectedPreset', preset.name);

                displayPresetMessageGrammar(' .', 'success');
                updatePresetJSONGrammar();
            });
        } else {
            presets.push(preset);
            localStorage.setItem('grammarPresets', JSON.stringify(presets));
            populatePresetSelectGrammar();
            $('#preset-select').val(preset.name).trigger('change');
            localStorage.setItem('lastSelectedPreset', preset.name);

            displayPresetMessageGrammar(' .', 'success');
            updatePresetJSONGrammar();
        }
    } catch (error) {
        console.error('   :', error);
        displayPresetMessageGrammar('   .   JSON.', 'danger');
    }
}


function displayPresetMessageGrammar(message, type) {
    const messageDiv = document.getElementById('preset-message');
    messageDiv.innerHTML = `<div class="alert alert-${type}">${message}</div>`;
    setTimeout(() => {
        messageDiv.innerHTML = '';
    }, 3000);
}

function getDefaultSettingsGrammar() {
    return {
        highlightErrors: true,
        addComments: false,
        highlightCorrected: true,
        checkGrammatical: false,
        checkSyntactic: false,
        checkStylistic: true,
        checkLogical: true,
        splitIntoChunks: false,
        chunkSize: 8,
        maxChars: 2000,
        temperature: 0.1,
        maxTokens: 4000,
        responseFormat: false,
        retryOnError: true,
        retryCount: 3,
        editPrompts: true,
        grammarModel: "anthropic/claude-3.5-sonnet:beta",
    };
}

function getDefaultPromptsGrammar() {
    return {
        'role': ' -    .',
        '': '-   ,      ,   .',
        '': '-   ,     .',
        '': '-   ,       .',
        '': '-   ,       .',
    };
}

async function checkGrammarGrammar() {
    try {
        const splitIntoChunks = document.getElementById('split-into-chunks-checkbox').checked;
        const chunkSize = parseInt(document.getElementById('chunk-size').value, 10) || 5;

        //      
        document.getElementById('grammar-results').innerHTML = '';
        resetProgressBarGrammar();

        showLoadingGrammar(true, splitIntoChunks);

        //   
        document.getElementById('filter-container').style.display = 'none';

        //   AbortController   
        window.abortController = new AbortController();

        //   
        const selectionText = await Word.run(async (context) => {
            const selection = context.document.getSelection();
            selection.load("text");
            await context.sync();
            return selection.text.trim();
        });

        const maxChars = parseInt(document.getElementById('max-chars').value, 10) || 100;

        if (selectionText.length > 0) {
            if (!splitIntoChunks && maxChars > 0 && selectionText.length > maxChars) {
                document.getElementById('grammar-results').innerHTML = `<p class="text-warning">    (${selectionText.length} ). ,   ${maxChars}      .</p>`;
                showLoadingGrammar(false);
                return;
            }

            const model = $('#grammar-model').val();
            let result = [];

            if (splitIntoChunks) {
                const chunks = splitTextIntoChunksGrammar(selectionText, chunkSize);
                const totalChunks = chunks.length;
                const progressBar = document.getElementById('progress-bar');
                const progressText = document.getElementById('progress-text');

                //       
                progressText.innerText = ` 0  ${totalChunks} `;

                for (let i = 0; i < chunks.length; i++) {
                    const chunkResult = await analyzeTextWithRetryGrammar(chunks[i], model, window.abortController.signal);
                    result = result.concat(chunkResult);

                    //   
                    const progressPercent = Math.round(((i + 1) / totalChunks) * 100);
                    progressBar.style.width = `${progressPercent}%`;
                    progressBar.innerText = `${progressPercent}%`;
                    progressText.innerText = ` ${i + 1}  ${totalChunks} `;
                }
            } else {
                const chunkResult = await analyzeTextWithRetryGrammar(selectionText, model, window.abortController.signal);
                result = result.concat(chunkResult);
            }

            //       
            window.grammarResults = result;

            displayResultsGrammar(result);
            await highlightTextGrammar(result);

            //   ,   
            if (result.length > 0) {
                document.getElementById('filter-container').style.display = 'block';
            }
        } else {
            console.log("  ");
            document.getElementById('grammar-results').innerHTML = '<p class="text-warning">,    .</p>';
            showLoadingGrammar(false); //  ,    
        }
    } catch (error) {
        if (error.name === 'AbortError') {
            console.log('  .');
            document.getElementById('grammar-results').innerHTML = '<p class="text-info"> .</p>';
        } else {
            console.error('Error in checkGrammarGrammar:', error);
            document.getElementById('grammar-results').innerHTML = '<p class="text-danger">   . ,   .</p>';
        }
    } finally {
        showLoadingGrammar(false);
        window.abortController = null;
    }
}

function cancelCheckGrammar() {
    if (window.abortController) {
        window.abortController.abort();
        showLoadingGrammar(false);
        window.abortController = null;
    }
}

function resetProgressBarGrammar() {
    const progressBar = document.getElementById('progress-bar');
    const progressText = document.getElementById('progress-text');
    if (progressBar) {
        progressBar.style.width = '0%';
        progressBar.innerText = '0%';
    }
    if (progressText) {
        progressText.innerText = ' 0  0 ';
    }
}

function splitTextIntoChunksGrammar(text, chunkSize) {
    const sentences = text.match(/[^.!?]+[.!?]+(\s|$)|[^.!?]+$/g) || [text];
    const chunks = [];
    for (let i = 0; i < sentences.length; i += chunkSize) {
        const chunk = sentences.slice(i, i + chunkSize).join(' ').trim();
        chunks.push(chunk);
    }
    return chunks;
}

async function analyzeTextWithRetryGrammar(text, model, signal) {
    const retryOnError = document.getElementById('retry-on-error-checkbox').checked;
    const retryCount = parseInt(document.getElementById('retry-count').value, 10) || 1;

    let attempts = 0;
    let lastError = null;

    while (attempts < retryCount) {
        try {
            const result = await analyzeTextGrammar(text, model, signal);
            return result;
        } catch (error) {
            lastError = error;
            attempts++;
            if (!retryOnError || attempts >= retryCount) {
                throw error;
            } else {
                console.warn(` ${attempts}  . ...`);
            }
        }
    }

    throw lastError;
}


async function analyzeTextGrammar(text, model, signal) {
    const apiKey = localStorage.getItem('apiKey');

    //    
    const errorTypes = [];
    const errorTypesMap = {};

    const prompts = {};
    const defaultPrompts = getDefaultPromptsGrammar();

    if (document.getElementById('check-grammatical').checked) {
        errorTypes.push('');
        errorTypesMap[''] = true;
        prompts[''] = document.getElementById('prompt-grammatical').value || defaultPrompts[''];
    }
    if (document.getElementById('check-syntactic').checked) {
        errorTypes.push('');
        errorTypesMap[''] = true;
        prompts[''] = document.getElementById('prompt-syntactic').value || defaultPrompts[''];
    }
    if (document.getElementById('check-stylistic').checked) {
        errorTypes.push('');
        errorTypesMap[''] = true;
        prompts[''] = document.getElementById('prompt-stylistic').value || defaultPrompts[''];
    }
    if (document.getElementById('check-logical').checked) {
        errorTypes.push('');
        errorTypesMap[''] = true;
        prompts[''] = document.getElementById('prompt-logical').value || defaultPrompts[''];
    }

    if (errorTypes.length === 0) {
        throw new Error('   ');
    }

    const errorTypesString = errorTypes.join(', ');

    //     
    const promptTextNote = document.getElementById('prompt-text-note').value || '';

    //   
    const promptRole = document.getElementById('prompt-role').value || defaultPrompts['role'];

    //   
    const temperature = parseFloat(document.getElementById('temperature').value) || 0.1;
    const maxTokens = parseInt(document.getElementById('max-tokens').value, 10) || 8000;
    const responseFormat = document.getElementById('response-format-checkbox').checked;

    //   
    const systemPrompt = `${promptRole}   -         : ${errorTypesString}.

${promptTextNote ? `   : ${promptTextNote}\n` : ''}

${Object.keys(prompts).map(type => `  "${type}" :\n${prompts[type]}\n`).join('\n')}

 :

-        .

-     .

-    .

-             ,  "can_edit": true    "text_to_change"   .

-           ,  "can_edit": false,   "suggestion"        "example"    .

- ,     JSON  .  ,            (\\"").

-     JSON-,    :
  1. "text":    (,   ).
  2. "comment":  .
  3. "errorType":   (${Object.keys(errorTypesMap).map(type => `"${type}"`).join(', ')}).
  4. "can_edit": true  false (,       ).
  5.  "can_edit"  true,   "text_to_change"   .
  6.  "can_edit"  false,   "suggestion"       "example"    .

 :

${getExampleErrorsGrammar(Object.keys(errorTypesMap))}

:

-  ** JSON-     .

- ,     JSON  .  ,            (\").

-   ,   : [];
`;

    const userPrompt = `,   :

"${text}";
`;

    try {
        const requestBody = {
            model: model,
            messages: [
                { role: "system", content: systemPrompt },
                { role: "user", content: userPrompt }
            ],
            temperature: temperature,
            max_tokens: maxTokens,
        };

        if (responseFormat) {
            requestBody.response_format = { "type": "json_object" };
        }

        const response = await fetch("https://openrouter.ai/api/v1/chat/completions", {
            method: 'POST',
            headers: {
                'Authorization': `Bearer ${apiKey}`,
                'Content-Type': 'application/json',
                'HTTP-Referer': 'https://www.office.com',
                'X-Title': 'Word Grammar Checker',
            },
            body: JSON.stringify(requestBody),
            signal: signal, //     
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        console.log('  :', data.choices[0].message.content.trim());

        //     JSON
        const jsonResponse = safeJsonParseGrammar(data.choices[0].message.content.trim());
        return jsonResponse;
    } catch (error) {
        if (error.name === 'AbortError') {
            console.log('  .');
            throw error;
        } else {
            console.error('Error in analyzeTextGrammar:', error);
            throw error;
        }
    }
}


//    JSON
function safeJsonParseGrammar(str) {
    try {
        return JSON.parse(str);
    } catch (error) {
        //     
        const fixedStr = str.replace(/\\?"/g, '\\"').replace(/\\\\"/g, '\\\"');
        try {
            return JSON.parse(fixedStr);
        } catch (innerError) {
            console.error('Failed to parse JSON after fixing:', innerError);
            throw innerError;
        }
    }
}

